import { yupResolver } from "@hookform/resolvers/yup";
import React, { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import * as yup from "yup";
import man from "../../../assets/images/man.png";
import woman from "../../../assets/images/woman.png";
import Helmet from "../../components/Helmet";
import Layout from "../../components/Layout";
import Checkbox from "../../shared/Checkbox";
import Grid from "../../shared/Grid";
import SVGIcon from "../../shared/SVGIcon";
import TextField from "../../shared/TextField";
import Upload from "../../shared/Upload";
import { useDispatch } from "react-redux";
import { SignupAction } from "../../../redux/reducer/signup";
import { useSelector } from "react-redux";

const defaultValues = {
  email: "",
  name: "",
  phone: "",
  sex: 0,
  password: "",
  retypePassword: "",
};

function SignUp({ history }) {
  const [isMale, setIsMale] = useState(true);
  const [avatar, setAvatar] = useState(null);
  const [isSubmitted, setIsSubmitted] = useState(false);

  const dispatch = useDispatch();

  const errorSignup = useSelector((state) => state.signup.error);

  const schema = yup
    .object({
      email: yup
        .string()
        .required("Please enter your email")
        .email("Email is not valid"),
      name: yup.string().required("Please enter your name"),
      phone: yup
        .string()
        .required("Please enter your phone number")
        .matches(
          /(84|0[1|3|5|7|8|9])+([0-9]{8})\b/,
          "Phone number is not valid"
        ),
      password: yup.string().required("Please enter password"),
      retypePassword: yup
        .string()
        .oneOf([yup.ref("password")], "Retype Password must be equal Password"),
    })
    .required();

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm({
    mode: "onSubmit",
    defaultValues,
    resolver: yupResolver(schema),
  });

  const handleSignUp = (values) => {
    setIsSubmitted(true);
    const signupData = new FormData();
    signupData.append("singleImage", avatar);

    for (const [key, value] of Object.entries(values)) {
      signupData.append(key, value);
    }

    signupData.delete("retypePassword");

    dispatch(SignupAction.signup(signupData));
  };

  useEffect(() => {
    if (isSubmitted && !errorSignup) history.push("/login");
  }, [isSubmitted, errorSignup, history]);

  return (
    <Helmet title="Signup">
      <Layout>
        <div className="sign-in">
          <div className="sign-in__top">
            <h1>Đăng kí để sử dụng Belo</h1>
            <small>
              Đã có tài khoản?
              <Link to="/login">Đăng nhập</Link>
            </small>
          </div>
          <form className="sign-in__form" onSubmit={handleSubmit(handleSignUp)}>
            <div className="sign-in__form__content">
              <Upload
                image={isMale ? man : woman}
                accept="image/png, image/jpeg, image/jpg"
                onUpload={(file) => setAvatar(file)}
              />
              <div>
                <div className="sign-in__form__content__title">Giới tính</div>
                <Grid col={2}>
                  <Controller
                    control={control}
                    name="sex"
                    defaultValue={0}
                    render={({ field: { onChange, value } }) => (
                      <>
                        <Checkbox
                          type="radio"
                          checked={isMale}
                          value={0}
                          onChange={(e) => {
                            onChange(e);
                            setIsMale(true);
                          }}
                        >
                          Nam
                        </Checkbox>
                        <Checkbox
                          type="radio"
                          checked={!isMale}
                          value={1}
                          onChange={(e) => {
                            onChange(e);
                            setIsMale(false);
                          }}
                        >
                          Nữ
                        </Checkbox>
                      </>
                    )}
                  />
                </Grid>
              </div>
              <Controller
                control={control}
                name="email"
                render={({ field: { onChange, name } }) => (
                  <div className="input-field">
                    <TextField
                      type="text"
                      label="Email"
                      inputChange={(e) => {
                        onChange(e);
                      }}
                      name={name}
                      error={!!errors[name]}
                    />
                    {errors && errors[name] && (
                      <div className="input-field__error">
                        {errors[name].message}
                      </div>
                    )}
                  </div>
                )}
              />
              <Controller
                control={control}
                name="name"
                render={({ field: { onChange, name } }) => (
                  <div className="input-field">
                    <TextField
                      type="text"
                      label="Tên"
                      inputChange={(e) => {
                        onChange(e);
                      }}
                      name={name}
                      error={!!errors[name]}
                    />
                    {errors && errors[name] && (
                      <div className="input-field__error">
                        {errors[name].message}
                      </div>
                    )}
                  </div>
                )}
              />
              <Controller
                control={control}
                name="phone"
                render={({ field: { onChange, name } }) => (
                  <div className="input-field">
                    <TextField
                      type="text"
                      label="Số điện thoại"
                      inputChange={(e) => {
                        onChange(e);
                      }}
                      error={!!errors[name]}
                    />
                    {errors && errors[name] && (
                      <div className="input-field__error">
                        {errors[name].message}
                      </div>
                    )}
                  </div>
                )}
              />
              <Controller
                control={control}
                name="password"
                render={({ field: { onChange, name } }) => (
                  <div className="input-field">
                    <TextField
                      type="password"
                      label="Mật khẩu"
                      inputChange={(e) => {
                        onChange(e);
                      }}
                      error={!!errors[name]}
                    />
                    {errors && errors[name] && (
                      <div className="input-field__error">
                        {errors[name].message}
                      </div>
                    )}
                  </div>
                )}
              />
              <Controller
                control={control}
                name="retypePassword"
                render={({ field: { onChange, name } }) => (
                  <div className="input-field">
                    <TextField
                      type="password"
                      label="Nhập lại mật khẩu"
                      inputChange={(e) => {
                        onChange(e);
                      }}
                      name={name}
                      error={!!errors[name]}
                    />
                    {errors && errors[name] && (
                      <div className="input-field__error">
                        {errors[name].message}
                      </div>
                    )}
                  </div>
                )}
              />
            </div>
            {errorSignup && (
              <div
                className="input-field__error"
                style={{ textAlign: "center", marginBottom: "1rem" }}
              >
                {errorSignup}
              </div>
            )}
            <button className="btn" type="submit">
              Đăng ký
            </button>
          </form>
        </div>
      </Layout>
    </Helmet>
  );
}

export default SignUp;
